import React, { Component } from 'react';
import api from '../../../Global/Api';
import {Row, Col, Card, Form, Button} from 'react-bootstrap';
import Aux from "../../../../hoc/_Aux";
import {Modal} from 'antd';

class App extends Component {

  state = {
    hotelfacilityid:  this.props.match.params.FacilityId,
    facility_image: null,
    facility_name:'',
    facility_status:'',
};

  componentDidMount (){
    this.fetchfacility();
  }

  success() {
    Modal.success({
      content: 'Successively Updated',
    });
  }
  
  error() {
    Modal.error({
      title: 'This is an error message',
      content: 'Error Occurred!!',
    });
  }

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value
    })
  };

  handleImageChange = (e) => {
    this.setState({
      image: e.target.files[0]
    })
  };

  fetchfacility = () => {
    this.setState({ loading: true });
    api.get(`/api/hotelfacility/${this.state.hotelfacilityid}`,{})
    .then(res => {
        this.setState({
          loading: false,
          facility_image: res.data.facility_image,
          facility_name:res.data.facility_name,
          facility_status:res.data.facility_status,
        });
      });
  };


  handleSubmit = (e) => {
    e.preventDefault();
    let form_data = new FormData();
    form_data.append('facility_name', this.state.facility_name);
    form_data.append('facility_status', this.state.facility_status);
    if(!this.props.match.params.FacilityId){
    form_data.append('facility_image', this.state.image, this.state.image.name);
    api.post(`/api/hotelfacility`, form_data, {
        headers: { 'Content-Type': 'multipart/form-data' }})
        .then(response => {
          if(response.status === 201){
            this.success();
          }
        })
        .catch(error => {
          this.error();
        });
    }
    else{
        console.log(this.state.image);
        if(this.state.image){form_data.append('facility_image', this.state.image, this.state.facility_image);}
        api.put(`/api/hotelfacility/${this.state.hotelfacilityid}`, form_data, {
            headers: { 'Content-Type': 'multipart/form-data' }})
            .then(response => {
              if(response.status === 200){
                this.success();
                this.setState({
                    facility_image:response.data.facility_image
                })
              }
            })
            .catch(error => {
              this.error();
            });
      }
  };
  

  render() {
    return (
      <Aux>
        <Row>
            <Col>
                <Card>
                    <Card.Header>
                        <Card.Title as="h5">Hotel Facility</Card.Title>
                    </Card.Header>
                    <Card.Body>
                        <Row>
                            <Col md={6}>
                                <Form onSubmit={this.handleSubmit}>
                                    <Form.Group controlId="exampleForm.ControlInput1">
                                        <Form.Label>Facility Name</Form.Label>
                                        <Form.Control type="text"  id='facility_name' value={this.state.facility_name} onChange={this.handleChange} required />
                                    </Form.Group>
                                    <Form.Group controlId="exampleForm.ControlInput1">
                                        <Form.Label>Upload Image</Form.Label>
                                        <Form.Control type="file"  id="image" accept="image/png, image/jpeg"  onChange={this.handleImageChange}  placeholder="Text" />
                                    </Form.Group>
                                    {/* <Form.Group controlId="formBasicChecbox"> 
                                        <Form.Check name="facility_status" type="checkbox" 
                                                  value={true}
                                                  label="Status" />
                                    </Form.Group> */}
                                    <Button variant="primary" type="submit">
                                        Submit
                                    </Button>
                                </Form>
                                
                                <p>{this.state.facility_status}</p>
                            </Col>
                            <Col md={6}>
                                <img alt={this.state.facility_name} className="img-fluid p-3" src={`http://34.93.135.63/${this.state.facility_image}`} ></img>
                            </Col>
                        </Row>
                    </Card.Body>
                </Card>
            </Col>
        </Row>
    </Aux>
    );
  }
}

export default App;